
body {
  background: radial-gradient(circle farthest-side at 0% 50%, #ededed 23.5%, rgba(240, 166, 17, 0) 0) 21px 30px, radial-gradient(circle farthest-side at 0% 50%, #e6e6e6 24%, rgba(240, 166, 17, 0) 0) 19px 30px, linear-gradient(#ededed 14%, rgba(240, 166, 17, 0) 0, rgba(240, 166, 17, 0) 85%, #ededed 0) 0 0, linear-gradient(150deg, #ededed 24%, #e6e6e6 0, #e6e6e6 26%, rgba(240, 166, 17, 0) 0, rgba(240, 166, 17, 0) 74%, #e6e6e6 0, #e6e6e6 76%, #ededed 0) 0 0, linear-gradient(30deg, #ededed 24%, #e6e6e6 0, #e6e6e6 26%, rgba(240, 166, 17, 0) 0, rgba(240, 166, 17, 0) 74%, #e6e6e6 0, #e6e6e6 76%, #ededed 0) 0 0, linear-gradient(90deg, #e6e6e6 2%, #ededed 0, #ededed 98%, #e6e6e6 0%) 0 0 #ededed;
  background-size: 40px 60px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.navicon {
  position: absolute;
  left: 30px;
  top: 30px;
}
.navicon img {
    width: 30px;
    height: 30px;
}
.slide-menu-enter-active, .slide-menu-leave-active {
  transition: all 0.5s ease-out;
}
.slide-menu-enter, .slide-menu-leave-to {
  -webkit-transform: translateX(-200px);
          transform: translateX(-200px);
}



